<template>
  <div class="chart-container">
    <div class="chart-title">{{title}}</div>
    <div id="option18" class="chart-content" :style="{'height': height + 'px', 'width': width + '%'}"></div>
    <div class="chart-footer" v-if="footer" :style="{'width' : width + '%'}">
      <span>{{footer}}</span>
    </div>
  </div>
</template>

<script>
import * as echarts from 'echarts'
export default {
  props: {
    title: {
      type: String,
      default: null
    },
    footer: {
      type: String,
      default: null
    },
    height: {
      type: Number,
      default: 400
    },
    width: {
      type: Number,
      default: 100
    },
    isMounted: {
      type: Boolean,
      default: false
    }
  },
  mounted () {
    // domecharts
    this.myChart = echarts.init(document.getElementById('option18'))
    // prettier-ignore
    this.chartOptionData = {
      tooltip: {
        trigger: 'axis',
        formatter: '{b} : {c}%'
      },
      color:['#909847'],
      xAxis: {
        data: ['', '', '','',''],
        axisLabel:{
          interval:0,
          rotate:-25,
        },
      },
      yAxis: {},
      dataGroupId: '',
      animationDurationUpdate: 500,
      series: {
        type: 'bar',
        id: 'sales',
        itemStyle: {
          borderRadius:[100,100, 0,0 ]
        },
        data: [
          {
            value: 16,
            groupId: '',
            itemStyle:{color:'#595f28'}
          },
          {
            value: 9,
            groupId: '',
            itemStyle:{color:'#71783a'}
          },
          {
            value: 9,
            groupId: '',
            itemStyle:{color:'#909847'}
          },
          {
            value: 6,
            groupId: '',
            itemStyle:{color:'#b0ba59'}
          },
          {
            value: 4,
            groupId: '',
            itemStyle:{color:'#e7de70'}
          },
        
        ],
        universalTransition: {
          enabled: true,
          divideShape: 'clone'
        }
      }
    }

    const drilldownData = [
      {
        dataGroupId: '',
        data: [
          ['', 22,],
          ['', 14],
          ['', 9],
          ['', 9],
          ['', 9],
          ['',7],
          ['',7],
          ['',6],
          ['',5],
          ['',4]
          
        ],

      },
      {
        dataGroupId: '',
        data: [
          ['', 66],
          ['', 38],
          ['', 14],
          ['', 14]
        ]
      },
      {
        dataGroupId: '',
        data: [
          ['', 13],
          ['', 11],
          ['', 11],
          ['', 9],
          ['', 8],
          ['',7],
          ['',7]
        ]
      },
      {
        dataGroupId: '',
        data: [
          ['', 78],
          ['', 23],
          ['', 17],
          ['', 2]
        ]
      },
      {
        dataGroupId: '',
        data: [
          ['', 26],
          ['', 25],
          ['', 21],
          ['', 18],
          ['', 16],
          ['',15],
          ['',13],
          ['',10]
        ]
      }
    ]
    this.myChart.on('click',  (event) => {
      if (event.data) {
        var subData = drilldownData.find(function (data) {
          return data.dataGroupId === event.data.groupId;
        });
        if (!subData) {
          return;
        }
        this.myChart.setOption({
          xAxis: {
            data: subData.data.map(function (item) {
              return item[0];
            })
          },
          series: {
            type: 'bar',
            id: 'sales',
            dataGroupId: subData.dataGroupId,
            data: subData.data.map(function (item) {
              return item[1];
            }),
            universalTransition: {
              enabled: true,
              divideShape: 'clone'
            }
          },
          graphic: [
            {
              type: 'text',
              left: 50,
              top: 20,
              style: {
                text: '',
                fontSize: 18
              },
              onclick:  ()=> {
                this.myChart.setOption(this.chartOptionData)
              }
            }
          ]
        })
      }
    })
  }, 
  data () {
    return {
      myChart: null,
      chartOptionData: null
    }
  },
  watch: {
    isMounted: {
      handler (newVal) {
        this.myChart.setOption(this.chartOptionData)
      }
    }
  }
}
</script>

<style>
.chart-title {
  font-size: 2em;
  padding: 20px 0;
  text-align: left;
  font-style: italic;
}
.chart-container {
  width: 100%;
  padding: 50px 0;
  background-color: #cfd6c5;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.chart-footer { 
  text-align: left;
}
</style>